/* COMMON */
@font-face {
  font-family: montserrat;
  src: url('/fonts/montserrat.woff');
}
@font-face {
  font-family: geometric;
  src: url('/fonts/geometric.ttf');
}

body {
    margin: 0;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    height: 100%;
    font-family: 'montserrat',monospace;
}

html{
    text-align: center;
}

#root{
    height: inherit;
}

.flex{
  display: flex;
}

.align-item-center{
  align-items: center;
}

.block{
  display: block;
}

$primary-color: #FF9F7D;
$title-color: #4A544C;
/* COMMON END */

/* NAVBAR */
.navbar-container{
    background-color: white;
    box-shadow: 1px 1px 5px lightgrey;
}

.navbar-container > div{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 0;
}
.navbar-container > div ul{
    display: flex;
    margin: 0;
}
.navbar-container > div li{
    list-style-type: none;
    margin: 0 1.3rem;
    cursor: pointer;
    font-weight: 500;
}
.navbar-container > div a{
    color: #4A544C;
    font-size: 0.75rem;
    font-weight: bold;
}
.navbar-container img{
    width: 250px;
}

.auth-nav{
  position: relative;
  background-color: white;
}
.auth-nav img{
  width: 250px;
}

.left-nav{
  position: absolute;
  left: 0;
  top: 0;
}
.left-nav{
  width: 27vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.sidebar-nav{
  margin-top: 4rem;
  border: 1px solid $primary-color;
  padding: 3rem 0;
  align-self: baseline;
  width: 100%;
}
.sidebar-element{
  display: flex;
  align-items: center;
  margin: 1.5rem 0 0 3rem;
  cursor: pointer;
}
.sidebar-element p{
  margin: 0 1rem 0;
}
.sidebar-element img{
  width: 15px;
}
.sidebar-element:hover{
  color: $primary-color;
}

.top-nav{
  display: flex;
  justify-content: flex-end;
  padding: 1rem;
}
.top-nav img{
  width: 15px;
  margin: 0 10px;
}
.top-nav img:nth-last-child(1){
  width: 30px;
}

.right-nav{
  display: flex;
  align-items: center;
}

.mobile-right-nav{
  display: flex;
  justify-content: center;
  align-items: center;
}

.logo {
    width: 120px;
    height: 31px;
    background: rgba(255, 255, 255, 0.2);
    margin: 16px 24px 16px 0;
    float: left;
}

.language-selector{
  font-weight: bold;
  border-top: 3px solid $primary-color;
  color: $primary-color;
  cursor: pointer;
  padding: 0 7px;
  margin-left: 1rem;
  margin-bottom: 0;
  text-transform: uppercase;
}

.hamburger{
  margin-left: 1rem !important;
}
.hamburger span, .hamburger span::before, .hamburger span::after{
  background-color: $title-color;
}
.hamburger:focus{
  outline: none;
}

@media(max-width: 550px){
  .navbar-container img{
    width: 150px;
  }
  .language-selector{
    font-size: 0.7rem;
  }
  .hamburger span, .hamburger span::before, .hamburger span::after{
    width: 20px;
  }
  .hamburger{
    margin-left: 5px !important;
  }
}
/* NAVBAR END*/

/* LAYOUT */
.ant-layout{
  height: inherit;
}

.ant-layout-content{
  background-color: white;
}

.padding-container{
  padding: 1rem 2rem;
  height: 100%;
}

.padded-container{
  width: 100%;
  height: 100%;
  padding: 0 1rem 1rem;
}

.nav-button{
  width: 250px;
  background: linear-gradient(to right, $primary-color, #E5805A) !important;
  color: white !important;
  border: none;
  font-weight: bold;
  padding: 0 !important;
  justify-content: center;
}
.nav-button span{
  font-size: 1rem !important;
  padding: 0 !important;
}

.title{
  font-weight: bold;
  color: $title-color;
  font-size: 1.3rem;
  margin: 3px;
  line-height: 1.5rem;
}

.subtitle{
  color: $primary-color;
  font-size: 0.7rem;
  font-family: geometric;
  letter-spacing: 3px;
  font-weight: bold;
}

.title-icon{
  width: 50px;
  color: $primary-color;
}

.ant-form-item{
  margin-bottom: 10px;
}
.ant-form-item input{
  font-size: 11px;
  padding: 5px 11px;
}

@media(max-width: 500px){
  .title{
    font-size: 1rem;
  }
}
/* LAYOUT END */

/* AUTH */
.auth-container{
  width: 100%;
  height: 100%;
  position: relative;
  background-color: #f3f3f3;
}

.auth-left-content{
  width: 35%;
  height: inherit;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 2rem;
}

.auth-right-content{
  width: 65%;
  height: inherit;
  min-width: 320px;
  background-color: #F6F6F6;
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 1rem 2rem;
}

.auth-right-content button{
  margin-top: 1rem;
}

.auth-bottom-text{
  font-size: 0.6rem;
  margin-top: 2rem;
  font-weight: bold;
}
.auth-bottom-text a{
  color: $primary-color;
}

.ant-form-item button{
  margin-top: 2rem;
}

.login-info-text{
  margin: 20px 3rem;
  font-size: 0.8rem;
  text-align: justify;
}

.auth-container .ant-form-item{
  max-width: 320px;
  margin: auto;
}

#basic_email{
  margin-bottom: 5px;
}

@media(max-width: 950px){
  .auth-left-content, .auth-right-content{
    width: 100%;
    position: relative;
  }
  .auth-left-content{
    padding: 3rem 0;
  }
  .auth-left-content form{
    margin: 3rem 0;
  }
}

@media(max-width: 550px){
  .auth-right-content{
    min-width: auto;
    height: auto;
  }
}
/* AUTH END */

// RECOVER PASSWORD
.recover-password-text{
  margin: 60px 0;
}
.recover-password-text h2{
  font-weight: bold;
  color: $primary-color;
  font-size: 1.2rem;
}
.recover-password-text h3{
  font-size: 0.7rem;
  max-width: 350px;
}

.recover-password-input{
  max-width: 400px;
  margin-bottom: 40px;
}
.recover-password-input button{
  width: 400px;
  max-width: 100% !important;
}

@media(max-width: 550px){
  .recover-password-input button{
    width: 100%;
  }
}
// RECOVER PASSWORD END

// REGISTER
.register-container{
  width: 100%;
  height: 100%;
  position: relative;
  background-color: #f3f3f3;
  display: flex;
}

.left-register{
  width: 35%;
  height: inherit;
  background-size: cover;
  background-position: center;
}
.left-register img{
  max-width: 320px;
  position: absolute;
  bottom: 5%;
  transform: translate(-50%, -50%);
}

.right-register{
  width: 65%;
  height: inherit;
  background-color: #F6F6F6;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1rem 2rem;
  overflow-y: auto;
}
.right-register button{
  margin-top: 1rem;
}

.register-bg{
  background-image: linear-gradient(to bottom, #F9C799AA, #FFFFFF00), url('/img/register/bg.png');
}
.register-form-bg{
  background-image: linear-gradient(to bottom, #F9C799AA, #FFFFFF00), url('/img/register/bg2.png');
}

.right-register-block{
  width: 65%;
  height: inherit;
  background-color: #F6F6F6;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1rem 2rem;
  overflow-y: auto;
}

.roles-boxes{
  display: flex;
  flex-wrap: wrap;
  max-width: 550px;
  justify-content: center;
}

.role-box{
  width: 220px;
  height: 180px;
  margin: 0.5rem;
  background-color: white;
  padding: 1rem;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  color: #CCCCCC;
  transition: all 200 ease-in-out;
  -webkit-transition: all 200 ease-in-out;
  -moz-transition: all 200 ease-in-out;
  -ms-transition: all 200 ease-in-out;
  -o-transition: all 200 ease-in-out;
}
.role-box img{
  width: 120px;
}
.role-box p{
  font-weight: bold;
  color: inherit;
  font-size: 1rem;
  margin: 0;
}

.rb-active{
  color: $primary-color;
}

.register-container .auth-bottom-text{
  font-size: 0.8rem;
  margin-top: 1rem;
}

.form-container input{
  font-size: 0.7rem;
}

.right-register-block > div > input{
  margin-bottom: 10px;
}

.multiple-inputs{
  display: flex;
}
.multiple-inputs input{
  margin: 0 auto 10px auto;
}
.multiple-inputs input:nth-child(even){
  margin: 0 5px 10px 5px;
}
.multiple-inputs .last-input{
  margin: 0 0 10px 5px !important;
}

.selector-input{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1rem;
}
.selector-input p{
  font-size: 0.8rem;
  margin: 0 1rem;
}
.selector-input input{
  min-width: 50px;
}

.beneficiary-title{
  border-top: 1px solid rgb(158, 158, 158);
  padding: 2px 0;
  text-align: left !important;
  font-size: 0.55rem;
  color: rgb(158, 158, 158);
}

.agent-selector{
  width: 100%;
  font-size: 0.7rem;
  text-align: left;
  margin-bottom: 5px;
  margin-top: 1.5rem;
}

.checkbox-input{
  margin: 1rem 0;
}
.checkbox-input span{
  font-size: 0.7rem;
}

.form-container .ant-input-affix-wrapper{
  margin: 4px 0;
}

.ant-checkbox-inner{
  border-color: $primary-color !important;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
}

.ant-checkbox-checked .ant-checkbox-inner{
  background-color: $primary-color;
}
.ant-checkbox-checked::after{
  outline: none;
  border: none;
}

.form-container :focus, .form-container :hover{
  outline: none;
}

@media(max-width: 950px){
  .left-register{
    display: none;
  }
  .right-register, .right-register-block{
    width: 100%;
  }
  .right-register{
    padding-top: 2rem;
  }
  .roles-boxes{
    margin: 1rem 0;
  }
  .role-box{
    width: 170px;
    height: 140px;
  }
  .role-box p{
    font-size: 0.8rem;
  }
  .role-box img{
    width: 80px;
  }
}
@media(max-width: 450px){
  .role-box{
    width: 112px;
    height: 110px;
  }
  .role-box p{
    font-size: 0.7rem;
  }
  .role-box img{
    width: 70px;
  }
}
@media(max-height: 770px){
  .right-register-block{
    display: block;
  }
}
// REGISTER END

// APP
.application-container{
  width: calc(73vw - 60px);
  margin: 0 0 auto auto;
  background-color: #F6F6F6;
  height: 100%;
  margin-right: 20px;
}

.appplication-content{
  padding: 2rem;
}

.app-top-icon{
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2rem 0 10px;
}
.app-top-icon img{
  width: 35px;
}
.app-top-icon p{
  margin: 0 0 0 1rem;
  color: $primary-color;
  text-transform: uppercase;
  font-size: 1.1rem;
}

.application-content h2{
  color: gray;
  font-size: 1rem;
  text-transform: uppercase;
}

.padded-app-container{
  padding: 1rem 2rem;
  max-width: 900px;
  margin: 0 auto;
}

#company-app{
  margin-bottom: 10px;
}

.beneficiary-message{
  text-align: left;
}
// APP END